body {
	min-width: 320px;
	color: $dark;
	background: $white;
	font-family: $lato;
	@include f(14px, 400, 1.43);
	@include font-smoothing;
	&.is-overflow-hidden {
		overflow: hidden; } }

[data-anim-from] {
	position: relative;
	opacity: 0; }
[data-anim-from="bottom"] {
	transform: translateY(50px); }

.text-yellow {
	color: $yellow; }

.row {
	margin: 0 -15px;
	@include clr; }
.col {
	float: left;
	padding: 0 15px;
	&-50 {
		width: 50%; }
	&-sm-100 {
		@include sm {
			width: 100%; } } }
