html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video { margin: 0; padding: 0; border: 0; vertical-align: baseline; }

html { -ms-text-size-adjust: 100%; -webkit-text-size-adjust: 100%; }

img, fieldset, a img { border: none; }

input[type="text"], input[type="email"], input[type="tel"], textarea { -webkit-appearance: none; }

input[type="submit"], button { cursor: pointer; }

input[type="submit"]::-moz-focus-inner, button::-moz-focus-inner { padding: 0; border: 0; }

textarea { overflow: auto; }

input, button { margin: 0; padding: 0; border: 0; }

div, input, textarea, select, button, h1, h2, h3, h4, h5, h6, a, span, a:focus { outline: none; }

ul, ol { list-style-type: none; }

table { border-spacing: 0; border-collapse: collapse; width: 100%; }

html { box-sizing: border-box; }

*, *:before, *:after { box-sizing: inherit; }

@media (prefers-reduced-motion: reduce) { * { transition: none !important; } }

input::-ms-clear { display: none; }

a { text-decoration: none; }

.icon-arrow-left { width: 0.89em; height: 1em; fill: currentColor; }

.icon-arrow-right { width: 0.89em; height: 1em; fill: currentColor; }

.icon-chevron { width: 0.89em; height: 1em; fill: currentColor; }

.icon-facebook { width: 0.53em; height: 1em; fill: currentColor; }

.icon-logo { width: 2.65em; height: 1em; fill: currentColor; }

body { min-width: 320px; color: #3F566E; background: #fff; font-family: "Lato", Helvetica, Arial, sans-serif; font-size: 14px; font-weight: 400; line-height: 1.43; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }

body.is-overflow-hidden { overflow: hidden; }

[data-anim-from] { position: relative; opacity: 0; }

[data-anim-from="bottom"] { transform: translateY(50px); }

.text-yellow { color: #FFDC45; }

.row { margin: 0 -15px; }

.row:after { content: " "; display: table; clear: both; }

.col { float: left; padding: 0 15px; }

.col-50 { width: 50%; }

@media only screen and (max-width: 767px) { .col-sm-100 { width: 100%; } }

.title-bigger { font-family: "Arbutus Slab", Helvetica, Arial, sans-serif; font-size: 100px; letter-spacing: 0; line-height: 0.76; }

@media only screen and (max-width: 767px) { .title-bigger { font-size: 70px; } }

.title-lg { font-family: "Arbutus Slab", Helvetica, Arial, sans-serif; font-size: 75px; letter-spacing: 0; line-height: 1; }

@media only screen and (max-width: 767px) { .title-lg { font-size: 42px; } }

.out { overflow: hidden; min-height: 100vh; }

.inner { position: relative; padding-left: 15px; padding-right: 15px; margin-left: auto; margin-right: auto; max-width: 1200px; }

.inner:after { content: " "; display: table; clear: both; }

body.is-pink .theme-fill { fill: #FF539E; }

.header { margin-bottom: 100px; text-align: center; padding-bottom: 47px; }

.header .logo { float: left; }

.header__title { text-transform: uppercase; color: #303A44; display: inline-block; }

.topic { margin-top: 125px; padding: 125px 0 148px; }

.topic__container { display: flex; flex-wrap: wrap; }

.topic__text-wrap { flex: 0 0 50%; min-width: 50%; padding-right: 100px; }

.topic__title { font-family: "Montserrat", Helvetica, Arial, sans-serif; font-size: 52px; font-weight: bold; line-height: 1.15; letter-spacing: 1.5px; color: #FF539E; }

.topic__descr { margin-bottom: 24px; opacity: 0.7; font-size: 14px; line-height: 1.43; color: #3F566E; }

.topic__btn .btn { font-family: Montserrat; font-size: 18px; font-weight: normal; line-height: 1.11; letter-spacing: 0px; color: #253240; border: none; background-color: #FF539E; }

.topic__img-wrap { flex: 0 0 50%; min-width: 50%; height: 370px; background-color: #fff; position: relative; text-align: center; font-size: 0; box-shadow: 0 33px 71px 0 rgba(63, 86, 110, 0.1), 0 0 24px 0 rgba(99, 246, 235, 0.04); }

.topic__img-wrap:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.topic__img { display: inline-block; vertical-align: middle; }

.topic__overlay { position: absolute; z-index: -1; top: 24px; left: 29px; width: 250%; height: 100%; background-color: #EFEFEF; }

.topic .theme-fill { fill: #FF539E; }

.title { font-family: "Montserrat", Helvetica, Arial, sans-serif; font-size: 20px; font-weight: 300; line-height: 2.3; letter-spacing: 0.6px; color: #333d47; text-transform: uppercase; }

.btn-text_sm { font-size: 10px; }

.btn-text .icon-arrow-right { font-size: 7px; margin-top: -0.6em; }

.more-services__content { display: flex; justify-content: space-between; align-items: center; }

.more-services__left { font-family: "Montserrat", Helvetica, Arial, sans-serif; font-weight: 300; font-size: 20px; letter-spacing: 0.6px; color: #333d47; }

.servisec-item { position: relative; font-family: "Arbutus Slab", Helvetica, Arial, sans-serif; font-size: 30px; margin-bottom: 7px; }

.servisec-item:before { content: ""; position: absolute; width: 4px; height: 28px; background-color: #FFDC45; left: -24px; top: 50%; transform: translateY(-50%); }
